import styles from "../css/calculator.module.css";


function ac() {
    // alert('ac');
    document.getElementById('txt').value = '';
}

function calculator() {

    function keyPress(txt) {
        document.getElementById('txt').value += txt;
    }
    function c() {
        // alert('hi');
        // getthetext box value
        var txtValue = document.getElementById('txt').value;
        // remove the last character 
        var end = String(txtValue).length - 1;
        txtValue = String(txtValue).substring(0, end);
        // // asign the result to text box
        document.getElementById('txt').value = txtValue;
    }
    function result() {
        // alert('result');
        // read the value
        var txtValue = document.getElementById('txt').value;
        // make decission by operator
        txtValue = eval(String(txtValue));
        // do the operations
        // show the result
        document.getElementById('txt').value = txtValue;

    }
    return (
        <div className={styles.calculator}>
            <div id="text1">
                <p><input id="txt" type="text" /></p>
            </div>

            <p className="clear"><input type="button" value="C" onClick={()=>c()} />
                <input type="button" value="AC" onClick={()=>ac()} />
            </p>
            <p><input type="button" value="7" onClick={()=>keyPress(7)} />
                <input type="button" value="8" onClick={()=>keyPress(8)} />
                <input type="button" value="9" onClick={()=>keyPress(9)} />
                <input type="button" value="/" onClick={()=>keyPress('/')} />
            </p>
            <p><input type="button" value="4" onClick={()=>keyPress(4)} />
                <input type="button" value="5" onClick={()=>keyPress(5)} />
                <input type="button" value="6" onClick={()=>keyPress(6)} />
                <input type="button" value="*" onClick={()=>keyPress('*')} />
            </p>
            <p><input type="button" value="1" onClick={()=>keyPress(1)} />
                <input type="button" value="2" onClick={()=>keyPress(2)} />
                <input type="button" value="3" onClick={()=>keyPress(3)} />
                <input type="button" value="-" onClick={()=>keyPress('-')} />
            </p>
            <p><input type="button" value="0" onClick={()=>keyPress(0)} />
                <input type="button" value="." onClick={()=>keyPress('.')} />
                <input type="button" value="+" onClick={()=>keyPress('+')} />
                <input type="button" value="=" onClick={()=>result()} />
            </p>
        </div>
    );
}
export default calculator;