import React from "react";
import styles from '../css/calculator.module.css';



class CalclatorNew extends React.Component {
    keyPress(txt) {
        document.getElementById('txt1').value += txt;
    }
    ac() {
        // alert('ac');
        document.getElementById('txt1').value = '';
    }
    c() {
        // alert('hi');
        // getthetext box value
        var txtValue = document.getElementById('txt1').value;
        // remove the last character 
        var end = String(txtValue).length - 1;
        txtValue = String(txtValue).substring(0, end);
        // // asign the result to text box
        document.getElementById('txt1').value = txtValue;
    }
    result() {
        // alert('result');
        // read the value
        var txtValue = document.getElementById('txt1').value;
        // make decission by operator
        txtValue = eval(String(txtValue));
        // do the operations
        // show the result
        document.getElementById('txt1').value = txtValue;

    }
    render() {
        return (
            <div className={styles.calculator}>
                <div id="text1">
                    <p><input id="txt1" type="text" /></p>
                </div>

                <p className="clear"><input type="button" value="C" onClick={() => this.c()} />
                    <input type="button" value="AC" onClick={() => this.ac()} />
                </p>
                <p><input type="button" value="7" onClick={() => this.keyPress(7)} />
                    <input type="button" value="8" onClick={() => this.keyPress(8)} />
                    <input type="button" value="9" onClick={() => this.keyPress(9)} />
                    <input type="button" value="/" onClick={() => this.keyPress('/')} />
                </p>
                <p><input type="button" value="4" onClick={() => this.keyPress(4)} />
                    <input type="button" value="5" onClick={() => this.keyPress(5)} />
                    <input type="button" value="6" onClick={() => this.keyPress(6)} />
                    <input type="button" value="*" onClick={() => this.keyPress('*')} />
                </p>
                <p><input type="button" value="1" onClick={() => this.keyPress(1)} />
                    <input type="button" value="2" onClick={() => this.keyPress(2)} />
                    <input type="button" value="3" onClick={() => this.keyPress(3)} />
                    <input type="button" value="-" onClick={() => this.keyPress('-')} />
                </p>
                <p><input type="button" value="0" onClick={() => this.keyPress(0)} />
                    <input type="button" value="." onClick={() => this.keyPress('.')} />
                    <input type="button" value="+" onClick={() => this.keyPress('+')} />
                    <input type="button" value="=" onClick={() => this.result()} />
                </p>
                <ul>
                    <li><a href="./calculator">Calclator</a></li>
                    <li><a href="./dommi">Dommi</a></li>
                </ul>
            </div>
        )
    }
}

export default CalclatorNew;