import React, { useState } from "react";
import Counter2 from "./counter2";


function Counter() {
    const [count, setCount] = useState(0);

    const buttonDecrease = () => {

        setCount(count - 1);
        console.log(count)
    }
    const buttonIncrease = () => {
        setCount(count + 1);
        console.log(count)
    }
    // let count = 0;
    return (<React.Fragment>
        <button onClick={() => buttonDecrease()}>-</button><span>{count}</span><button onClick={() => buttonIncrease()}>+</button>
        <Counter2></Counter2>
    </React.Fragment>);
}

export default Counter;