

const initialState = {
    count: 0
};

function counterReducer(state = initialState, action) {

    // just gonna leave this blank for now
    // which is the same as `return undefined;`
    switch (action.type) {
        case 'INCREMENT':
            return {
                count: state.count + 1
            };
        case 'DECREMENT':
            return {
                count: state.count - 1
            };
        default:
            return state;
    }
}

export default counterReducer;