import { SETEMPLOYEE } from "../actions/employeeData.action";

const initialState = {
    employee: {
        "Name": "",
        "Age": 0,
        "Email": "",
        "Date of joining": "",
        "Phone number": "",
        "Street": "",
        "City": "",
        "Zip": "",
        "Region": "",
        "Country": "",
        "Info": "",
        "EmployeeID": 0
    }
};

function EmployeeReducer(state = initialState, action) {
    console.log('reducer');
    console.log(action);
    // just gonna leave this blank for now
    // which is the same as `return undefined;`
    switch (action.type) {
        case SETEMPLOYEE:
            // return {
            //     ...state, ...action.payload
            // };
            let newState = {
                employee: action.payLoad
            }
            console.log(action.payLoad);
            console.log(action);
            console.log(newState);
            return newState;
        default:
            return state;
    }
}

export default EmployeeReducer;