import Styles from "../css/login.module.css";

function Login() {
    function hi(){
        alert('hi');
    }
    return (
        <div className={Styles.loginForm}>
            <div>
                <p>UserName</p>
                <input type="text" />
            </div>
            <div>
                <p>Password</p>
                <input type="password" />
            </div>
            <div className="button">
                <button onClick={()=>hi()}>
                    login
                </button>
            </div>
        </div>
    );
}

//exporting imortant
export default Login;