
import React, { Component } from 'react';
import Styles from '../css/table.module.css';
// import { useState } from 'react';
// import Select from 'react-select';
// import { getElementError } from '@testing-library/react';

class Pagenation extends Component {
    // constructor(props) {
    //     super(props);
    // }
    state = {
        page: 1,
        pagelimit: 10 //options 5,10,15,20
    }
    buttons = [1, 2, 3, 4, 5, 6];
    data = this.props.data;
    PageLimitation = [
        { "value ": 5, "lable": 5 },
        { "value ": 10, "lable": 10 },
        { "value ": 15, "lable": 15 },
        { "value ": 20, "lable": 20 }

    ]
    orginalData = this.data.slice();
    generateRow = (data) => {
        return (<tr>
            {data}
        </tr>)
    }
    generateHead = (data) => {
        return (
            <th>{data}</th>
        );
    }
    generateData = (data,index) => {
        return (
            <td key={index}>{String(data)}</td>
        );
    }
    // generateEachHead = () => {

    //     let keys = Object.keys(this.data[0]);
    //     for (let i = 0; i < keys.length; i++) {
    //         this.generateRow(this.generateHead(keys[i]))
    //     }


    // }
    displayHead = () => {
        // console.log(keys);
        let keys = Object.keys(this.data[0]);
        return keys.map((eachKey) => {
            return (this.generateHead(eachKey));

        });


    }
    // generateEachData = (index) => {

    //     let data = this.data[index];
    //     let key = Object.keys(this.data[index]);
    //     let data1;
    //     for (let i = 0; i < 4; i++) {
    //         data1 = this.generateData(data[key[i]])
    //         console.log(data[key[i]])
    //     }

    // }
    displayData = (singledata) => {

        let data = Object.values(singledata);
        // let key = Object.keys(this.data[0]);
        return data.map((datas,index) => {
            return (this.generateData(datas,index))
        })
    }
    displayBody = (pageData) => {
        return pageData.map((singleData) => {
            let singleRow = this.displayData(singleData)
            return this.generateRow(singleRow);
        })
    }
    changePage = (pageNumber) => {
        let start = (pageNumber - 1) * this.state.pagelimit;
        let end = start + this.state.pagelimit;
        return this.data.slice(start, end)
    }
    changeDisplayPage = (page) => {
        this.setState({ page: page })
        // this.state.page = page;
        // console.log(this.state);
    }
    displayButtons = () => {
        return this.buttons.map((buttonNmbers,index) => {
            return <button key={index} onClick={() => this.changeDisplayPage(buttonNmbers)}>{buttonNmbers}</button>
        })
    }
    displayButtonsByData = () => {
        // let data = this.data;
        let buttonsLimit = Math.ceil(this.data.length / this.state.pagelimit);
        let numberOfButtons = [];
        // if (data.length % this.state.pagelimit !== 0) {
        //     buttonsLimit += 1;
        // }
        for (let i = 1; i <= buttonsLimit; i++) {
            numberOfButtons.push(i);
        }
        this.buttons = numberOfButtons;
        return this.displayButtons();
        // return numberOfButtons.map((buttonNmbers) => {
        //     return <button onClick={() => this.changeDisplayPage(buttonNmbers)}>{buttonNmbers}</button>
        // })
    }

    Previous = (page) => {
        return this.changeDisplayPage(page - 1)
    }
    next = (page) => {
        return this.changeDisplayPage(page + 1)
    }

    pageLimitChange(event) {

        // return this.setState.pagelimit = document.getElementById("list1").values;
        // console.log(document.getElementById("list1").value);
        this.setState({ pagelimit: event.target.value });

    }

    clickSearch = () => {
        var search = document.getElementById('search').value;
        
        // var reslt = this.data.filter((a) => { return ((a.userId) === search || a.id === search ||
        //      a.title === search || a.body === search) });
        var reslt = this.orginalData.filter((a) => {
            const keys = Object.keys(a);
            // console.log(keys)
            
            return keys.some((b) => {
                if (typeof (a[b]) === 'string')
                    return a[b].includes(search);
                else
                    return String(a[b]).includes(search);
            })
            
            // return keys.some((b) => {
            //     if (typeof (a[b]) === 'string')
            //         return a[b].match(searchRegx).length >0;
            //     else
            //         return  String(a[b]).includes(search);
            // })
            // return ((a.userId) == search || a.id == search ||
            //     a.title === search || a.body === search)
        });


        console.log(search);
        console.log(this.data = reslt);
        this.setState({page:1})


    }

    render() {
        let data = this.changePage(this.state.page);
        // let data = this.data.slice(0,10);
        let displayHead = this.displayHead();
        // let displayData = this.displayData();
        let displayTableBody = this.displayBody(data);
        // let displayButtons = this.displayButtons();
        let displayButtonsByData = this.displayButtonsByData();
        // let pageLimitChange = this.pageLimitChange();



        return (
            <div className={Styles.Table1}>
                <div id='buttonDiv'>
                    <button className='forMostLeft' onClick={() => this.changeDisplayPage(this.buttons[0])}> &lt;&lt;  </button>
                    <button className='previous' onClick={() => this.changeDisplayPage(this.state.page - 1)}> Previous  </button>
                    {displayButtonsByData}
                    <button className='next' onClick={() => this.changeDisplayPage(this.state.page + 1)}> Next  </button>
                    <button className='forMostRight' onClick={() => this.changeDisplayPage(this.buttons.length)}> &gt;&gt;  </button>
                    <label htmlFor='list1'>Data Per Page</label>
                    <select name="pageLimits" id="list1" defaultValue={this.state.pagelimit} onChange={(event) => this.pageLimitChange(event)} >
                        <option value={5}>5</option>
                        <option value={10}>10</option>
                        <option value={15}>15</option>
                        <option value={20}>20</option>
                    </select>
                    <input id='search' type='text' />
                    <button onClick={() => this.clickSearch()}>Search</button>

                </div>
                <table>
                    <thead id="heading"><tr>{displayHead}</tr></thead>
                    <tbody id="data">{displayTableBody}</tbody>
                </table>

                <button onClick={() => this.changeDisplayPage(2)}>2</button>
            </div>
        );
    }
}

export default Pagenation;