// import "../css/signUp.css";
import styles from "../css/signUp.module.css"

function SignUp() {
  function  signUp2(){
        alert('help')
    }
    return (

        <div className={styles.SignUp}>
            <div>
                <p className="first"><i className="fa-solid fa-user"> Name</i></p>
                <input id="userName" type="text" />
            </div>

            <div>

                <p><i className="fa-solid fa-key"> Password</i> </p>
                <input id="password" type="password" />
            </div>

            <div>
                <p><i className="fa-solid fa-user"> Re-enter Password</i></p>
                <input id="reEnterPassword" type="password" />
            </div>
            <div id="button">
                <button><i className="fa-solid fa-arrow-left"></i> Back</button>
                <button onClick={()=>signUp2()}><i className="fa-solid fa-arrow-right-to-bracket"></i> SignUp</button>
            </div>
            
        </div>
    );
}

export default SignUp;