import React, { Component } from "react";
import image from "../image/male.jfif"
import CardStyles from "../css/employeeCard.module.css"
import TableStyle from '../css/table.module.css';
import { Link } from "react-router-dom";
import { connect } from "react-redux";
// import DataStyles from "../css/employeeData.module.css"

class EmployeeData extends Component {
    constructor(props) {
        super(props);

    }
    state = {}
    // data = {
    //     "Name": "Aurora",
    //     "Age": 53,
    //     "Email": "ante.blandit@disparturient.ca",
    //     "Date of joining": "10/06/2019",
    //     "Phone number": "1628101902299",
    //     "Street": "493 Iaculis Rd.",
    //     "City": "Lewiston",
    //     "Zip": "42591-180",
    //     "Region": "Maine",
    //     "Country": "Sudan",
    //     "Info": "est. Nunc ullamcorper, velit in aliquet lobortis, nisi nibh lacinia",
    //     "EmployeeID": 101
    // }
    generateRow = (data) => {
        return (<tr>
            {data}
        </tr>)
    }
    generateHead = (data) => {
        return (
            <th>{data}</th>
        );
    }
    generateData = (data) => {
        return (
            <td>{String(data)}</td>
        );
    }
    displayData = (eachData) => {
        // var data = this.props.data
        // var data = Object.values(eachData);
        // var keys = Object.keys(eachData);
        // var eachRow;
        // for (let i = 0; i < eachData.length; i++) {
        //     return (
        //         this.generateHead(keys[i])
        //     )
        // }

        var keys = Object.keys(eachData);
        return keys.map((e) => {
            return (
                <tr key={e}>
                    <th>{e}</th>
                    <td>{eachData[e]}</td>
                </tr>
            )
        })

    }

    displayCard = (cardData) => {

        return (<div className={CardStyles.card}>
            <img alt="img" width={150} src={image} />
            <p className="pName"><label className="name" id="idName" htmlFor="name">Name: {cardData.Name}</label></p>
            {/* <button onDoubleClick={() => this.viewData()}>Back</button><br /> */}
            <Link to="/EmployeeCard">Back</Link>

        </div>)

    }


    render() {

        return (
            <div className={CardStyles.employeeDataContainer}>
                <div className={CardStyles.card}>   {this.displayCard(this.props.data)}</div>
                <div className={TableStyle.table}><table className={TableStyle.Table1}>
                    <tbody>
                        {this.displayData(this.props.data)}
                    </tbody>
                </table></div>
            </div>
        );
    }
}


function mapStateToProps(state) {
    return {
        data: state.employee.employee
    }
}

export default connect(mapStateToProps)(EmployeeData);